.section-01.section-01-health {
    background: url(../img/overlay/Group87.jpg) no-repeat center center;
    background-attachment: fixed;
    background-size: 100% auto;
    background-position-y: 0px;
    @media screen and (max-width: 1340px) {
        background-size: auto 300px;
    }
    @media screen and (max-width: 700px) {
        background-size: auto 250px;
    }
    @media screen and (max-width: 387px) {
        .main-title-h {
            font-size: 24px;
            line-height: 40px;
        }
    }
}

.main-title-h {
    max-width: 800px;
    span {
        color: #FFCC24;
    }
}
.red-subtitle-text-h {
    font-weight: bold;
}
.sub-title-with-fire .-fire-icons-h {
    &:before {
        background: url(../img/overlay/022---Yoga.svg) no-repeat center center/cover;
        width: 49.83px;
        height: 49.83px;
        margin-bottom: -7px;
    }
    &:after {
        background: url(../img/overlay/exercise.svg) no-repeat center center/cover;
        width: 45.52px;
        height: 44px;
        margin-bottom: -7px;
    }
}

.section-03-h {
    background: none;
}
.setGoal {

    // background: url(../img/overlay/Polygon3.jpg) no-repeat center center/cover;
    min-height: 320px;
    text-align: center;
    color: #fff;
    padding: 29px 0 0 34px;
    position: relative;
    overflow: hidden;
    .container {
        max-width: 680px;
    }
    .triangle {
        position: absolute;
        z-index: -1;
        width: 130%;
        height: 100%;
        top: -0px;
        left:-15%;
    }

    h4 {
        text-transform: uppercase;
        font-size: 24px;
    }
    .setGoal-content {
        font-size: 24px;
        font-weight: bold; 
        line-height: 33px;
        margin-top: 16px;
    }
    .setGoal__yellow {
        color: #FFCC24;
        font-size: 24px;
        margin-top: 27px;
        line-height: 40px;
        &-upper {
            font-weight: bold;
            text-transform: uppercase;
        }
    }
    
}

.days30 {
    @media screen and (max-width: 700px) {
        padding: 0px 20px;
    }
    .container {
        max-width: 680px;
    }
    h2 {
        text-align: center;
        margin-top: 44px;
    }
    .list {
        margin-top: 26px;
        li {
            i {
                color: #27A43C;
                
            }
            span {
                font-size: 18px;
            }
        }
    }
}


.decidedAct {
    background: url(../img/overlay/545489-P.jpg) no-repeat center center/cover;
    padding: 45px 0 37px 0;
    background-attachment: fixed;
    

    .container {
        overflow: hidden;
    }
    @media screen and (max-width: 700px) {
        padding: 45px 20px;
    }
    &__title {
        color: #fff;
        font-family: Oswald,sans-serif;
        font-size: 44px;
        text-align: center;
        line-height: 57px;
        
