        margin-top: 40px;
        
        li {
            
            span {
                font-size: 18px;
            }
        }
    }
    .program-list-content {
        font-size: 18px;
        font-weight: bold;
        span {
            color: #27A43C;
            position: relative;
            &:after {
                content: url('../img/overlay/Path48.svg');
                display: block;
                position: absolute;
                top: 23px;
                right: 0;
            }
        }
        
