    .primary-btn {
        margin-top: 50px;
    }

}

.change {
    background: #F1F1F1;
    padding: 55px 0;
    font-size: 18px;
    .container {
        max-width: 680px;
    }
    h2 {
        margin-bottom: 40px;
    }
    .d-flex {
        display: flex;
        justify-content: space-between;
    }
    .change__content {
        
        line-height: 20px;
        font-size: 18px;
        p {
            line-height: 1.2;
        }
    }
    .change__img {
        width: 40.88541666666667%;
        display: inline;
        float: right;
        margin-left: 20px;
        margin-bottom: 5px;
        
        img {
            width: 100%;
            height: auto;
        }
        div {
            width: 100%;
            text-align: center;
            color: #fff;
            text-transform: uppercase;
            padding: 14px 0;
            background: #0F1238;
            margin-top: -5px;
            font-family: 'PT Sans', sans-serif;
            font-weight: bold;
            font-size: 17px;
        }
        
    }
    @media screen and (max-width: 700px) {
        padding: 55px 20px;
        .change__content {
            
            font-size: 16px;
        }
    }
    @media screen and (max-width: 576px) {
        .change__content {
            font-size: 15px;
        }
        .change__img {
            
            margin-left: 10px;
            
        }
    }
    @media screen and (max-width: 460px) {
        .img-inside {
            width: 100%;
            display: flex;
            .change__img {
                width: 90%;
                margin: 0 auto;
                margin-bottom: 20px;
                div {
                    font-size: 15px;
                }
            }
        }
            
    }
}

.stepForward {
    padding: 42px 0 30px 0;
    font-size: 18px;
