}

.section-09-health {
    padding-bottom: 40px;
    .container {
        max-width: 680px;
    }
    .author {
        box-shadow: 0 0px 10px rgba(0,0,0,.16);
        margin-top: 36px;
        margin-bottom: 36px;
        padding: 27px 21px 21px 36px;
    }
    .author .image  {
        display: flex;
            align-items: center;
        
        img{
            box-shadow: none;
            margin-bottom: -20px;
        }
    }
    ul {
        padding-left: 26px;
        li {
            font-size: 18px;
            margin-top: 15px;
            position: relative;
            &:before {
                content: "";
                display: block;
                position: absolute;
                width: 11px;
                height: 11px;
                border-radius: 50%;
                background: #27A43C;
                top: 5.5px;
                left: -26px;
            }
        }
    }
    p {
        font-size: 18px;
    }
}

.section-08__health2 {
    background: #F2F2F2;
    padding-top: 56px;
    padding-bottom: 88px;
    .container-health {
        max-width: 680px;
    }
    
    &__title {
        text-align: center;
        p {
            font-size: 18px;
            letter-spacing: 0.2px;
        }
        &-main {
            margin-top: 10px;
            margin-bottom: 40px;
            font-size: 28px;
            font-weight: bold;
            color: #0F1238;
            padding: 20px 13px 25px 13px;
            span {
                font-style: italic;
            }
            border-top: 1px solid #F90000;
            border-bottom: 1px solid #F90000;
        }
    }
    .motive-list {
        .motive-item-health {
            background: #fff;
            margin-top: 40px;
            padding-right: 5px;
            min-height: 254px;
            .image {
                width: auto;
                height: auto;
            }
            .health2-content {
                display: flex;
                .info {
                    padding-right: 10px;
                    max-width: 400px;
                }
                .title {
                    font-size: 26px;
                    font-weight: bold;
                    color: #0F1238;
                    text-transform: none;
                }
                .text-healt__border {
                    width: 215px;
                    text-align: center;
                    border-left: 1px solid #707070;
                    padding-left: 0px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    
                    &-1 {
                        div:nth-child(1) {
                            font-size: 14px;
                        }
                        div:nth-child(2) {
                            font-size: 18px;
                            font-weight: bold;
                            color: #4A4A4A;
                            margin-top: 5px;
                        }
                    }
                    &-2 {
                        div:nth-child(1) {
                            font-size: 14px;
                            font-weight: bold;
                            color: #0F1238;
                        }
                        div:nth-child(2) {
                            font-size: 24px;
                            font-weight: bold;
                            color: #37097D;
                            margin-top: 5px;
                        }
                    }
                    
                }
            }
            @media screen and (max-width: 1025px) {
                .health2-content {
                    justify-content: space-around;
                    .text {
                        text-align: center;
                        
                    }
                    
                }
                
            }
            @media screen and (max-width: 576px) {
                .health2-content {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    .info {
                        padding-right: 0px;
                        margin: 0 auto;
                        padding-top: 10px;
                    }
                    .title {
                        font-size: 22px;
                        
                    }
                    .text {
                        text-align: center;
                        font-size: 16px;
                    }
                    .text-healt__border {
                        margin: 0 auto;
                        margin-top: 10px;
                        border-left: none;
                    }
                    .text-healt__border-1 {
                        margin-bottom: 7px;
                    }
                }
                
            }
        }
    }
    .stepForward-border {
        padding: 27px 10px 15px 10px;
        
        &-title {
            text-align: center;
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        p   {
            font-size: 16px;
            font-weight: normal;
            text-align: center;
            margin-bottom: 0;
        }
    }
    .text-after-list-content {
        font-size: 18px;
        text-align: center;
        margin-top: 45px;
        span {
            font-weight: bold;
        }
    }
    .primary-btn.primary-btn-new {
        margin-top: 40px;
        width: 570px;
        font-size: 24px;
        font-weight: bold;
        font-style: normal;
        padding: 0;
        background: url(../img/overlay/Path3.png) no-repeat center center/contain;

    }
    .primary-btn.primary-btn-new:hover {
        filter: contrast(1.7) drop-shadow(2px 7px 6px rgba(0, 0, 0, .3));
    }
    @media screen and (max-width: 576px) {
        .primary-btn.primary-btn-new {
            width: auto;
            font-size: 18px;
        }
        padding-bottom: 48px;
    }
    @media screen and (max-width: 450px) {
        .primary-btn.primary-btn-new {
            width: auto;
            font-size: 14px;
            background-size: 100% 70%;
        }
        padding-bottom: 38px;
    }
    @media screen and (max-width: 340px) {
        .primary-btn.primary-btn-new {
            width: auto;
            font-size: 13px;
        }
        padding-bottom: 28px;
    }
}

